import React from "react";

export default ({ data,data2 }) => 

  <div>
  {console.log(data)}    
    <h1>About: {data.site.siteMetadata.title}  {data.allLettersJson.edges[0].node.value} {data.allLettersJson.edges[0].node.type}</h1>
    <p>
      We're the only site running on your computer dedicated to showing the best
      photos and videos of pandas eating lots of food.
    </p>
  </div>
export const query = graphql`
query AboutQuery {
    site {
        siteMetadata {
            title
        }
    }
    allLettersJson {
      edges {
        node {
            value
            type
        }
      }
    }
}
`



